.topbar {
    width: 100%;
    height: 70px;
    display: flex;
    justify-content: center;
    align-items: center;
    padding: 0 4%;
    box-sizing: border-box;
    flex-wrap: wrap;
}

.topbarleftalign {
    display: flex;
    align-items: center;
    height: 100%;
}

.logo {
    background-image: url(./media/logo.svg);
    height: 100px;
    width: 220px;
    margin-right: 50px;
    background-size: cover;
}

.logostaking {
    font-weight: 800;
    font-size: 30px;
    user-select: none;
}

.connectwalletbutton {
    height: 50px;
    background-color: #113CFC;
    display: flex;
    justify-content: center;
    align-items: center;
    color: white;
    cursor: pointer;
    padding: 0 25px;
    font-weight: 700;
    border-radius: 12px;
    position: absolute;
    top: 0px;
    right: 20px;
}

.connectwalletbuttonactive {
    background-color: #7DEBA2;
    color: white;
}

.contentcontainer {
    display: flex;
    align-items: center;
    flex-direction: column;
    position: absolute;
    top: 25px;
    width: 100%;
}

.infotext {
    margin-top: 4%;
    font-weight: 800;
    text-align: center;
    font-size: 20px;
}

.eligibleWallet {
    margin-top: 100px;
    padding: 10px 12px;
    border: 1px solid black;
    font-size: 22px;
    border-radius: 11px;
    cursor: pointer;
    background-color: #006eff;
    color: white;
}

@media only screen and (max-width: 800px) {
    .connectwalletbutton {
        margin-top: 100px;
    }
    .topbar{
        margin-bottom: 100px;
    }
}